.about {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3em;
}

.about__name {
  color: var(--clr-primary);
}

.about__role {
  margin-top: 1.2em;
}

.about__desc {
  font-size: 1rem;
  max-width: 900px;
  text-align: center;
}

.about__desc,
.about__resume{
  margin-top: 2.4em; /* Increased spacing */
}

.about__socials{
  margin-top: -0.4em; /* Increased spacing */
}


.about .link--icon {
  margin-right: 0.3em;
  margin-left: 0.3em;
  display: flex; /* Ensure icons are aligned correctly */
  justify-content: center;
}

.about .btn--outline {
  padding: 0.5em 1em;
  text-align: center;
  align-items: center;
  display: flex;
  justify-content: center;
  margin-bottom: 2.4em; /* Additional space to icons beneath */
}

@media (max-width: 600px) {
  .about {
    align-items: center; /* Center alignment in mobile view */
  }

  .about .link--icon {
    margin: 0.4em; /* Adjust margins for mobile view */
  }
}
