/* Contact.css */
.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px 20px;
  background-color: #f9f9f9;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: auto;
}

.contact .section__title {
  margin-bottom: 20px;
  font-size: 2em;
  color: var(--clr-primary);
}

.contact .contact__form {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.contact .form-group {
  margin-bottom: 1.5em;
}

.contact .form-group label {
  display: block;
  margin-bottom: 0.5em;
  font-weight: bold;
}

.contact .form-group input,
.contact .form-group textarea {
  width: 100%;
  padding: 0.75em;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1em;
  margin-bottom: 10px;
}

.contact .form-group textarea {
  height: 150px;
  resize: vertical;
}

.contact .btn {
  align-self: center;
  padding: 0.75em 1.5em;
  border: 2px solid var(--clr-primary);
  background-color: transparent;
  color: var(--clr-primary);
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s, color 0.3s;
}

.contact .btn:hover {
  background-color: var(--clr-primary);
  color: #fff;
}

.contact .success-message {
  margin-top: 20px;
  color: green;
  font-weight: bold;
}