.project {
  padding: 2em;
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.project:hover {
  transform: translateY(-10px);
  box-shadow: 0 10px 20px rgba(0,0,0,0.2);
}

.project:active {
  transform: translateY(-5px);
}

.project__description {
  margin-top: 1em;
}

.project__stack {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__stack-item {
  margin: 0.5em;
  font-weight: 500;
  font-size: 0.8rem;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}

.project__status {
  display: block;
  height: 1px;
  width: 100%;
  margin-top: 5px;
  background: linear-gradient(to right, transparent, var(--clr-primary) 10%, var(--clr-primary) 90%, transparent);
}

.project__status.in-progress {
  background: linear-gradient(to right, transparent, #FFC107 10%, #FFC107 90%, transparent);
}
